import {
  Button,
  FormControl,
  FormControlLabel,
  FormLabel,
  Radio,
  RadioGroup,
  TextField,
  Typography,
} from "@mui/material";
import React, { useEffect } from "react";
import { useFormik } from "formik";
import { useParams, useNavigate } from "react-router-dom";
import Loader from "./Loader";
import Helmet from "react-helmet";
import swal from "sweetalert2";

const Test = (props) => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [data, setData] = React.useState({
    title: "",
    questions: [],
    id: "",
  });

  useEffect(() => {
    const asyncFunc = async () => {
      const req = await fetch(`http://178.63.132.247:6345/api/tests/${id}`, {
        method: "get",
      });
      const { data, err } = await req.json();
      if (err) {
        return navigate("/");
      }

      setData(data);
    };
    if (!data.title) {
      asyncFunc();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data.title]);

  const initialValues = {
    nickname: "",
  };
  for (const question of data.questions) {
    initialValues[question.id] = "";
  }

  const formik = useFormik({
    initialValues,
    onSubmit: async (values) => {
      const nickname = String(values.nickname);
      const copyValues = JSON.parse(JSON.stringify(values));
      delete copyValues["nickname"];
      const req = await fetch(
        "http://178.63.132.247:6345/api/send-results-test",
        {
          method: "post",
          body: JSON.stringify({
            testId: data.id,
            nickname,
            answers: copyValues,
          }),
          headers: {
            "Content-Type": "application/json",
          },
        }
      );

      const { err, ok } = await req.json();

      if (err || !ok) {
        return swal.fire({
          icon: "error",
          text: err || " !",
          title: "  ",
        });
      }

      swal.fire({
        icon: "success",
        text: "      !",
        title: " ",
      });
    },
    validate(values) {
      const errors = {};

      for (const question of data.questions) {
        if (!question.required) {
          continue;
        }

        if (!values[question.id]) {
          errors[question.id] = "  !";
        }
      }

      return errors;
    },
  });

  if (!data.title) {
    return <Loader />;
  }

  return (
    <form onSubmit={formik.handleSubmit} className="center">
      <Helmet>
        <title>{data.title}</title>
      </Helmet>
      <Typography style={{ marginTop: 30, marginBottom: 30 }} variant="h4">
        {data.title}
      </Typography>
      <hr />
      <div className="container" style={{ marginTop: 30, marginBottom: 30 }}>
        <TextField
          name="nickname"
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
          fullWidth
          required
          label="  "
          variant="outlined"
        />
      </div>
      <hr />
      <div
        className="container questions"
        style={{ marginTop: 30, marginBottom: 30 }}
      >
        {data.questions.map((question) => {
          return (
            <FormControl
              error={formik.errors[question.id]}
              key={`question-${question.id}`}
            >
              <FormLabel>
                {question.text}
                {question.required ? "*" : ""}
              </FormLabel>
              <RadioGroup
                value={formik.values[question.id] || ""}
                onChange={formik.handleChange(question.id)}
              >
                {question.answers.map((answer) => (
                  <FormControlLabel
                    key={`answer-${answer.id}`}
                    value={answer.variant}
                    control={<Radio />}
                    label={answer.variant}
                  />
                ))}
              </RadioGroup>
            </FormControl>
          );
        })}

        <Button
          fullWidth
          type="submit"
          variant="contained"
          style={{ marginTop: 20 }}
        >
          
        </Button>
      </div>
    </form>
  );
};

export default Test;
