import { Button, TextField, Typography } from "@mui/material";
import { useFormik } from "formik";
import swal from "sweetalert2";
import React from "react";
import { Helmet } from "react-helmet";

export default function CreateTest() {
  const formik = useFormik({
    initialValues: {
      title: "",
      code: "",
    },
    validateOnChange: true,
    onSubmit: async (values) => {
      const req = await fetch("http://178.63.132.247:6345/api/create-test", {
        method: "post",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          title: values.title,
          questions: JSON.parse(values.code),
        }),
      });

      const { err, id } = await req.json();
      if(err){
        return swal.fire({
          icon: 'error',
          text: err,
          title: "  ",
        });
      }

      swal.fire({
        icon: 'success',
        html: `<a href="${document.location.origin}/#/tests/${id}">${document.location.origin}/tests/${id}</a>`,
        title: " "
      })
    },
    validate: (values) => {
      const errors = {};

      const { code, title } = values;
      try {
        JSON.parse(code);
      } catch (e) {
        errors["code"] = "   !";
      }

      if (!title) {
        errors.title = "  !";
      }

      return errors;
    },
  });
  return (
    <div className="center">
      <Typography style={{ marginTop: 30, marginBottom: 30 }} variant="h4">
         
      </Typography>
      <hr />
      <form className="container" onSubmit={formik.handleSubmit}>
        <Helmet>
          <title> </title>
        </Helmet>
        <TextField
          style={{ marginTop: 30, marginBottom: 30 }}
          name="title"
          fullWidth
          label="  "
          onChange={formik.handleChange}
          value={formik.values.title}
          helperText={formik.errors.title}
          error={!!formik.errors.title}
        />
        <TextField
          fullWidth
          label="    JSON"
          multiline
          helperText={formik.errors.code}
          error={!!formik.errors.code}
          rows={19}
          name="code"
          onChange={formik.handleChange}
          value={formik.values.code}
        />
        <Button
          fullWidth
          type="submit"
          variant="contained"
          style={{ marginTop: 20 }}
        >
          
        </Button>
      </form>
    </div>
  );
}
